body{background-color:#4CB5F5;font-family:"Lato", sans-serif}*{margin:0;padding:0;box-sizing:border-box}.sidebar{position:fixed;width:15rem;height:100%;background-color:#1C4E80;display:flex;flex-direction:column;justify-content:space-around}.sidebar .brand{color:white;font-size:1.6rem;display:flex;justify-content:center;align-items:center;margin:2rem 0;font-weight:900}.sidebar .links ul{margin-bottom:3rem}.sidebar .links ul .active{border-right:0.3rem solid white}.sidebar .links ul .active a{color:white}.sidebar .links ul li{border-right:0.3rem solid transparent;margin:1rem 0;list-style-type:none;transition:0.3s ease-in-out}.sidebar .links ul li a{margin:0 3rem;text-decoration:none;color:white;font-size:1.1rem;display:flex;grid-gap:1rem;gap:1rem}.sidebar .links ul li:hover{border-right-color:white}.sidebar .links ul li:hover a{color:#4CB5F5}.sidebar .pro{color:white;background-color:#4CB5F5;display:flex;flex-direction:column;align-items:center;grid-gap:0.6rem;gap:0.6rem;margin:0 2rem;border-radius:2rem;padding:1rem;text-align:center}.sidebar .pro .image img{height:2rem}.sidebar .pro button{color:#1C4E80;font-weight:900;padding:0.5rem 1rem;border:none;border-radius:2rem;cursor:pointer}.dashboard{margin-left:15rem;padding:2rem;height:100%;color:white}.dashboard .navbar{display:flex;justify-content:space-between}.dashboard .navbar .container{display:flex}.dashboard .navbar .container button{height:99%;color:white;background-color:#0d88e6;padding:0.2rem 1.2rem;border:none;border-radius:1.5rem;font-size:0.9rem;font-weight:600;cursor:pointer}.dashboard .navbar .container button:active{background-color:#1C4E80}.dashboard .navbar .container .search{background-color:white;display:flex;width:30rem;margin-right:0.5rem;justify-content:left;align-items:center;border-radius:2rem;padding:0.5rem 1rem;color:#0d88e6;grid-gap:1rem;gap:1rem;font-size:1.5rem}.dashboard .navbar .container .search input{border:none;color:#0d88e6;background-color:white}.dashboard .navbar .container .search input:focus{outline:none}.dashboard .navbar .profile{display:flex;align-items:center;grid-gap:1rem;gap:1rem;margin-left:1rem}.dashboard .navbar .profile .bell{font-size:1.4rem;color:#0d88e6}.dashboard .navbar .profile .image img{height:3rem;border-radius:3rem}.dashboard .grid{margin-top:3rem;height:100%}.dashboard .grid .one{margin:2rem 0}.dashboard .grid .one .milestones{display:flex;justify-content:space-between;background-color:#1C4E80;padding:0.8rem;border-radius:1rem}.dashboard .grid .one .milestones .milestone{display:flex;justify-content:center;align-items:center;grid-gap:1rem;gap:1rem;border-right:3px solid #0d88e6;padding-right:3rem}.dashboard .grid .one .milestones .milestone:last-of-type{border-right:none}.dashboard .grid .one .milestones .milestone .icon{padding:1rem;font-size:1.4rem;border-radius:1rem;color:white;background-color:#0d88e6}.dashboard .grid .one .milestones .milestone h2{color:#0d88e6}.dashboard .grid .two{height:100%;display:grid;grid-template-columns:0.7fr 1.5fr;grid-gap:1rem;gap:1rem}.dashboard .grid .two .twitter{height:80%;background-color:white;border-radius:1rem;margin-top:3rem;display:flex;flex-direction:column;align-items:center}.dashboard .grid .two .twitter h1{color:#1C4E80;text-align:center;margin-top:0.3rem}.dashboard .grid .two .twitter .logo{display:flex;flex-direction:column;align-items:center}.dashboard .grid .two .twitter .logo img{height:2.5rem;margin-top:0.3rem}.dashboard .grid .two .lstm_model{height:100%;background-color:white;border-radius:1rem;display:flex;flex-direction:column;align-items:center}.dashboard .grid .two .lstm_model h1{color:#1C6DD0;text-align:center;margin-top:0.6rem}.dashboard .grid .three{margin-top:1.2rem;height:100%;display:grid;grid-template-columns:0.7fr 1.5fr;grid-gap:1rem;gap:1rem}.dashboard .grid .three .news{height:95%;background-color:#0d88e6;border-radius:1rem}.dashboard .grid .three img{width:100%;height:100%;border-radius:1rem}.dashboard .grid .three .indicators{height:95%;background-color:#0d88e6;border-radius:1rem}.dashboard .grid .three .indicators img{width:100%;height:100%;border-radius:1rem}.copyright{text-align:center;font-weight:600;font-size:1rem}.copyright:hover{color:#1C4E80}

